
@import "variables";

/* MOBILE PORTRAIT to LANDSCAPE */
@media only screen and (min-width: 250px) and (max-width: 767px) {
    header{
        margin: 0;
        padding: 25px;
    }
    .hero_slider{
        .slider-content {
            margin-left: 25px;
             h3 {
                font-size: 14px;
                line-height: 130%;
            }
            h2 {
                font-size: 46px;
                line-height: 100%;
            }
        }
       .hero-image{
            height: 810px;
       }
       .slick-next{
            left: 25px;
       }
       .pagingInfo{
            left: 180px;
       }
    } 

}

